                    loading: false,
                    fromAccounts: myInitObject.fetchStatusData.fromAccounts,
                    fromSymbol: myInitObject.fetchStatusData.fromSymbol,
                    toAccounts: myInitObject.fetchStatusData.toAccounts,
                    toSymbol: myInitObject.fetchStatusData.toSymbol,
                    fromOpenOrderInfos: myInitObject.fetchStatusData.fromOpenOrderInfos.length > 0
