                    blockloader: myInitObject.fetchStatusData.fromOpenOrderInfos.length > 0 ? false : true,
                    total: myInitObject.fetchStatusData.fromOpenOrderInfos.length > 0 && myInitObject.fetchStatusData.fromOpenOrderInfos[0].order_type == 0
